import React from "react";
import firebase from "firebase";
import { useHistory } from "react-router-dom";
import "./AddCamp.css";
import MapAutocomplete from "./MapAutocamplete";
import { v4 as uuidv4 } from "uuid";
import styled from "styled-components";
import { RiDeleteBin2Line } from "react-icons/ri";
import { BiPencil } from "react-icons/bi";
import PopupEditData from "./PopupEditData";

const Infobox = styled.div`
  margin: 30px;
  padding: 30px;
  width: 90%;
  border-style: solid;
  border-width: 1px;
  border-color: gray;
  border-radius: 10px;
  display: flex;
  /* justify-content: space-between; */
`;

const Info = styled.div`
  margin-right: 20px;
`;

const Delete = styled.button`
  font-size: 20px;
  border-style: none;
  background-color: white;
  cursor: pointer;
  &:hover {
    color: red;
  }
`;

// const Edit = styled.button`
//   font-size: 20px;
//   border-style: none;
//   background-color: white;
//   cursor: pointer;
//   &:hover {
//     color: blue;
//   }
// `;

function AddCamp() {
  const [name, setName] = React.useState("");
  const [locationDetail, setLocationDetail] = React.useState("");
  const [guests, setGuests] = React.useState("");
  const [stars, setStars] = React.useState("");
  const [price, setPrice] = React.useState("");
  const [comment, setComment] = React.useState("");
  const [topicTag, setTopicTag] = React.useState("");
  const [discountTag, setDiscountTag] = React.useState("");
  const [otherTag, setOtherTag] = React.useState("");
  const [commentCount, setCommentCount] = React.useState(0);
  const [product, setProduct] = React.useState([]);
  const [image, setImage] = React.useState(null);
  const [address, setAddress] = React.useState("");
  const [placeId, setPlaceId] = React.useState("");
  const [latitude, setLatitude] = React.useState({});
  const history = useHistory();

  const GetAddress = (addressdata) => {
    setAddress(addressdata[0]);
    setPlaceId(addressdata[1]);
    setLatitude(addressdata[2]);
  };

  // const OnFileChange = (e) => {
  //   // Get Files
  //   for (let i = 0; i < e.target.files.length; i++) {
  //     const newFile = e.target.files[i];
  //     const id = uuidv4();
  //     newFile["id"] = id;
  //     setImage((prevState) => [...prevState, newFile]);
  //   }
  // };

  React.useEffect(() => {
    firebase
      .firestore()
      .collection("product")
      .onSnapshot((collectionSnapshot) => {
        const data = collectionSnapshot.docs.map((doc) => {
          const id = doc.id;
          return { ...doc.data(), id };
        });
        setProduct(data);
      });
  }, []);

  function onSubmit() {
    const documentRef = firebase.firestore().collection("product").doc();
    const imageRef = firebase.storage().ref("Product-image/" + documentRef.id);
    const metadata = {
      contentType: image.type,
    };
    imageRef.put(image, metadata).then(() => {
      imageRef.getDownloadURL().then((imageURL) => {
        console.log(commentCount);
        documentRef
          .set({
            name,
            guests,
            stars,
            price,
            imageURL,
            address,
            placeId,
            latitude,
            comment,
            commentCount,
            topicTag,
            discountTag,
            otherTag,
            // id: firebase.firestore.collection("product"),
          })
          .then(() => {
            window.alert("成功新增露營地");
            // history.push("/");
          });
      });
    });
  }

  function renderRoom(item) {
    return (
      <Infobox>
        <Info>露營地名稱：{item.name}</Info>
        <Info>地址：⌲{item.address}</Info>
        <Info>床位人數：{item.guests} guests</Info>
        <Info>NT.{item.price}/night</Info>
        <PopupEditData item={item} />
        <Delete
          onClick={() => {
            deleteroom(item.id);
          }}
        >
          <RiDeleteBin2Line />
        </Delete>
      </Infobox>
    );
  }

  function deleteroom(item) {
    firebase
      .firestore()
      .collection("product")
      .doc(item)
      .delete()
      .then((error) => {
        alert("成功刪除");
      });
  }

  return (
    <div className="rooms__backstage">
      <div className="rooms__backstage__title">上架露營地</div>
      <div className="rooms__backstage__campaing">
        <div>
          <input
            className="rooms__backstage__campaing__input"
            type="file"
            onChange={(e) => setImage(e.target.files[0])}
            // multiple
            // onChange={OnFileChange}
          />

          <input
            className="rooms__backstage__campaing__input"
            placeholder="露營地名稱"
            value={name}
            onChange={(e) => setName(e.target.value)}
          />
          {/* <input
          className="rooms__backstage__campaing__input"
          placeholder="露營地地點"
          value={locationDetail}
          onChange={(e) => setLocationDetail(e.target.value)}
        /> */}
          <MapAutocomplete placeaddress={GetAddress} />
          <input
            className="rooms__backstage__campaing__input"
            placeholder="露營地人數"
            value={guests}
            onChange={(e) => setGuests(e.target.value)}
          />
          <input
            className="rooms__backstage__campaing__input"
            placeholder="露營地評分"
            value={stars}
            onChange={(e) => setStars(e.target.value)}
          />
          <input
            className="rooms__backstage__campaing__input"
            placeholder="露營地價格"
            value={price}
            onChange={(e) => setPrice(e.target.value)}
          />
          <input
            className="rooms__backstage__campaing__input"
            placeholder="露營地介紹"
            value={comment}
            onChange={(e) => setComment(e.target.value)}
          />
        </div>
        <div className="rooms__infor">
          露營標籤
          <form>
            露營主題：
            <select
              className="select"
              value={topicTag}
              onChange={(e) => setTopicTag(e.target.value)}
            >
              <option value="Seaview">seaview</option>
              <option value="Mountainview">Mountainview</option>
              <option value="SwimmingPool">Swimming Pool</option>
              <option value="Barbecue">Barbecue</option>
            </select>
          </form>
          <form>
            地點類型：
            <select
              className="select"
              value={discountTag}
              onChange={(e) => setDiscountTag(e.target.value)}
            >
              <option value="懶人露營">懶人露營</option>
              <option value="親子露營">親子露營</option>
              <option value="露營車">露營車</option>
              <option value="免搭帳篷">免搭帳篷</option>
            </select>
          </form>
          <form>
            其他資訊：
            <select
              className="select"
              value={otherTag}
              onChange={(e) => setOtherTag(e.target.value)}
            >
              <option value="大草原">大草原</option>
              <option value="夜景">夜景</option>
              <option value="遊樂設施">遊樂設施</option>
              <option value="可攜帶寵物">可攜帶寵物</option>
            </select>
          </form>
          <button className="send__btn" onClick={() => onSubmit()}>
            送出
          </button>
        </div>
      </div>

      <div className="campaing">
        <div className="rooms__backstage__title">已上架商品</div>
        {product.map((item) => renderRoom(item))}
      </div>
    </div>
  );
}

export default AddCamp;
